import React, { Component } from 'react';
import { SIZES_MAP, DEFAULT_SIZE } from './Constants';
import { Transition } from 'react-transition-group';
import Svg from './StyledSvg';
import './UISpinner.css';

export default class UISpinner extends Component {
  static defaultProps = {
    size: 'medium',
    isCompleting: false,
    invertColor: false,
    onComplete: () => {}
  };
  constructor(props) {
    super(props);
    this.state = {
      phase: ''
    };
  }

  enter = () => {
    const delay = 'DELAY';
    if (delay) {
      this.setState({ phase: 'DELAY' });
    } else {
      this.setState({ phase: 'LEAVE' });
    }
  };

  idle = () => {
    this.setState({ phase: 'IDLE' });
  };

  exit = () => {
    this.setState({ phase: 'LEAVE' });
  };

  endListener = (node, done) => {
    const executeCallback = event => {
      if (this.state.phase === 'DELAY') {
        this.setState({ phase: 'ENTER' });
        this.endListener(node, done);
      } else {
        done();
      }
      return node && node.removeEventListener('animationend', executeCallback);
    };
    return node && node.addEventListener('animationend', executeCallback);
  };

  validateSize = () => {
    const { size } = this.props;
    const spinnerSize = SIZES_MAP[size] || size;
    return typeof spinnerSize === 'number' ? spinnerSize : DEFAULT_SIZE;
  };

  render() {
    const size = this.validateSize();
    const { phase } = this.state;
    const { invertColor, isCompleting } = this.props;
    const divSize = {
      height: size,
      width: size
    };
    return (
      <div className="Out">
        <Transition
          addEndListener={this.endListener}
          appear
          in={!isCompleting}
          mountOnEnter
          unmountOnExit
          onEnter={this.enter}
          onEntered={this.idle}
          onExit={this.exit}
          onExited={() => this.props.onComplete}
          ref={node => {
            this.transitionNode = node;
          }}
        >
          <div className="UISpinner" size={size} style={divSize}>
            <Svg size={size} invertColor={invertColor} phase={phase} />
          </div>
        </Transition>
      </div>
    );
  }
}
