import React from 'react';
export const getContainerAnimation = phase => {
  if (phase === 'DELAY') {
    return 'UISpinner__svg--delay';
  }
  if (phase === 'ENTER' || phase === 'IDLE') {
    return 'UISpinner__svg--idle';
  }
  if (phase === 'LEAVE') {
    return 'UISpinner__svg--leave';
  }
  return 'UISpinner__svg';
};

const Svg = props => {
  const { size, invertColor, phase } = props;
  const classCSS = getContainerAnimation(phase);
  // using RGBA give more control over color (white : skyblue-700)
  const getBKStrokeColor = invertColor
    ? 'rgba(255,255,255, 0.2)'
    : 'rgba(52,152,219, 0.1)';
  // define the color of the spinner (white : gradient)
  const getStrokeColor = invertColor ? 'white' : 'url(#LoadingSpinnerGradient)';

  return (
    <svg
      className={classCSS}
      viewBox="0 0 50 50"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      size={size}
      height={size}
      width={size}
    >
      <defs>
        <linearGradient id="LoadingSpinnerGradient">
          <stop offset="0%" stopColor="#3498db" stopOpacity=".9" />
          <stop offset="20%" stopColor="#3498db" stopOpacity=".8" />
          <stop offset="60%" stopColor="#3498db" stopOpacity=".7" />
          <stop offset="90%" stopColor="#3498db" stopOpacity=".6" />
        </linearGradient>
      </defs>
      <circle
        cx="25"
        cy="25"
        r="22"
        stroke={getBKStrokeColor}
        strokeWidth="6"
      />
      <circle
        className="UISpinner__ring"
        cx="25"
        cy="25"
        r="22"
        stroke={getStrokeColor}
        strokeWidth="6"
      />
    </svg>
  );
};

export default Svg;
