import React, { Component } from 'react';

import styled, { keyframes } from 'styled-components';
import { theme, mixins, media, Section, A, H3, P } from '../../styles';

import Image from '../../images/dashboard.jpg';
import ImageW from '../../images/dashboard.webp';

const Header = styled.header`
  display: block;
  margin-bottom: 30px;
`;

const Name = styled.h2`
  position: relative;
  padding-bottom: 18px;
  font-family: ${theme.fonts.SFProText};
  font-weight: 300;
  color: ${theme.colors.darkblue};

  .name {
    display: block;
    margin-bottom: 10px;
    font-size: 64px;
    line-height: 48px;
    font-weight: 400;
    will-change: transform, opacity;
  }

  span:last-child {
    font-weight: 100;
    letter-spacing: 1.5px;
  }

  &:before {
    content: '';
    position: absolute;
    display: block;
    bottom: 0;
    left: 0;
    width: 28px;
    height: 3px;
    border-radius: 5px;
    background: #fb7000;
  }
`;

const SaaS = styled.div`
  margin-bottom: 20px;
`;

const DashImg = styled.img`
  position: relative;
  width: 100%;
  border-radius: ${theme.borderRadius};
  transition: ${theme.transition};
  border-radius: 6px;
  box-shadow: 0 4px 8px -2px rgba(0, 0, 0, 0.1), 0 0 1px rgba(0, 0, 0, 0.1);
  margin: 20px 0 50px 0;
  padding: 0;
  overflow: hidden;
  background: red;
  background-image: url(${ImageW});
  /* background-position: center;  */
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100px;

  &:hover {
    box-shadow: 0 36px 64px 0 rgba(0, 0, 0, 0.1), 0 6px 30px 0 rgba(0, 0, 0, 0.05);
  }
`;

export default () => {
  return (
    <div>
      <Header>
        <Name>
          <span className="name" style={{ transitionDelay: '100ms' }}>
            Archive 10
          </span>
          <span>Web app</span>
        </Name>
      </Header>

      <div>
        <H3>Redefining SaaS solution</H3>
        <P>
          One of the responsibilities at GlobalRelay has been designing for Archive10 - a 6 years
          old high-performance cloud archiving platform designed to meet the privacy, compliance,
          and security needs of the financial sector and other highly regulated industries. Used
          primarily by compliance teams from Scotiabank, General Societe, HSBC, stockbrokers, etc.
        </P>
      </div>
      <DashImg src={Image} srcset={ImageW} alt="Dashboard for Compliance Report">
      
      </DashImg>
    </div>
  );
};

