import React, { Component } from 'react';
import icons from './icons/UIicons.json';
import './UIIcons.css';

export function findIcon(name, iconsObj = icons) {
  const icon = iconsObj.filter(obj => obj.name === name);
  if (icon.length === 0) {
    return false;
  } else if (icon.length > 1) {
    throw new Error('⚠️ multiple icons found .... 🤪🤪 ');
  } else {
    return icon[0];
  }
}

export function getSVGData(iconName) {
  const icon = findIcon(iconName);
  return icon ? icon.svgData : false;
}

export function svgShapes(svgData) {
  const svgElements = Object.keys(svgData)
    .filter(key => svgData[key])
    .map(svgProps => {
      const data = svgData[svgProps];

      if (svgProps === 'circles') {
        return data.map((circle, index) => {
          const circleProps = {
            cx: circle.cx,
            cy: circle.cy,
            r: circle.r,
            key: `circle${index}`
          };
          return <circle {...circleProps} />;
        });
      } else if (svgProps === 'paths') {
        return data.map((path, index) => (
          <path d={path.d} key={`key${index}`} />
        ));

        return '';
      }
    });

  return svgElements;
}

export function isPrefixed(name) {
  return name.split('--')[0] === 'icon';
}

const UIicon = ({
  className,
  description,
  fill,
  fillRule,
  fillOpacity,
  height,
  name,
  size,
  role,
  style,
  width,
  iconRef,
  ...other
}) => {
  const icon = isPrefixed(name) ? findIcon(name) : findIcon(`icon--${name}`);
  const props = {
    className,
    fill,
    fillRule,
    size: size || icon.size,
    fillOpacity: fillOpacity || icon.fillOpacity,
    height: size,
    name: isPrefixed ? name : `icon--${name}`,
    role,
    style,
    viewBox: icon.viewBox,
    width: size,
    ref: iconRef,
    ...other
  };
  const svgContent = icon ? svgShapes(icon.svgData) : '';

  return (
    <svg {...props} arial-label={description} alt={description}>
      <title>{name} </title>
      {svgContent}
    </svg>
  );
};

UIicon.defaultProps = {
  name: 'bell',
  fillOpacity: 1,
  fill: '#34495e',
  fillRule: 'evenodd',
  role: 'icon',
  description: '👋🏼 Provide a description 🤙🏻'
};
export { icons };
export default UIicon;
