//  @ts-check

import React, { Component, Fragment } from 'react';
import classNames from 'classnames';
import './uibutton.css';
import UISpinner from '../UISpinner';
import UIicon from '../UIIcons';

// used to avoid stays blur on Chrome
export function handleMouseUpByBlurring({ currentTarget }) {
  currentTarget.blur();
}

export default class UIButton extends Component {
  static defaultProps = {
    tabIndex: 0,
    size: 'medium',
    kind: 'secondary',
    disabled: false,
    loading: false,
    fullWidth: false
  };

  iconName = icon =>
    icon === true
      ? console.warn(' ⚠️ Warning, Please choose an icon ⚠️ ⚠️ ⚠️')
      : icon;

  elementColor() {
    const kindBtn = this.props.kind;

    return kindBtn === 'alert' ||
      kindBtn === 'success' ||
      kindBtn === 'primary' ||
      kindBtn === 'darkNight'
      ? true
      : false;
  }

  iconColor = () => {
    const kind = this.props.kind;

    if (this.elementColor(kind)) {
      return '#fff';
    } else if (kind === 'neutralIsDark') {
      return '#34495e';
    } else {
      return '#2a2a2a';
    }
  };

  render() {
    const {
      children,
      tabIndex,
      onClick,
      onBlur,
      onFocus,
      kind,
      size,
      className,
      fullWidth,
      disabled,
      loading,
      icon
    } = this.props;
    const iconCorProps = this.iconColor(kind);
    const iconNameProps = this.iconName(icon);
    const spinnerColor = this.elementColor(kind);

    const isDisabled = disabled || loading;

    const buttonClasses = classNames(className, {
      uibutton: true,
      [`uibutton--${kind}`]: kind,
      [`uibutton--${size}`]: size,
      [`uibutton--${size}__iconOnly`]: icon && !children,
      [`uibutton--${kind}__disabled`]: isDisabled && 'disabled',
      'uibutton--fullWidth': fullWidth,
      'uibutton--loading': loading
    });

    const iconMarkup = icon ? (
      <span className="uibutton__icon">
        <UIicon name={iconNameProps} size="20px" fill={iconCorProps} />
      </span>
    ) : null;

    const child = children ? <Fragment>{children}</Fragment> : null;

    const spinner = loading ? (
      <span className="uibutton__loader">
        <UISpinner
          size="small"
          accessibilityLabel="Loading"
          invertColor={spinnerColor}
        />
      </span>
    ) : null;

    const content =
      iconMarkup || spinner ? (
        <span className="uibutton__content">
          {spinner}
          {iconMarkup}
          <span className={icon && children ? 'uibutton__iconSpace' : null}>
            {child}
          </span>
        </span>
      ) : (
        <span className="uibutton__content">
          <span>{child}</span>
        </span>
      );

    return (
      <button
        className={buttonClasses}
        onMouseUp={handleMouseUpByBlurring}
        disabled={isDisabled}
        onClick={onClick}
      onFocus={onFocus}
      onBlur={onBlur}
      >
        {content}
      </button>
    );
  }
}
