import React from 'react';
import posed from 'react-pose';
import Waypoint from 'react-waypoint';
import Component from '@reach/component-component';

import theme from '../../styles/theme';

// opacity 300ms ${theme.easing}, transform 300ms ${theme.easing};

const ElItems = posed.div({
  visible: {
    opacity: 1,
    y: 0,
    //   transition: { type: 'spring', stiffness: 50, damping: 100 },
    transition: {
      duration: 1000,
      ease: [0.645, 0.045, 0.355, 1],
    },
  },
  hidden: {
    y: 20,
    opacity: 0,
  },
  initialPose: 'hidden',
});
const Sect = props => (
  <Component initialState={{ isOpen: false }}>
    {({ setState, state }) => (
      <Waypoint topOffset={100} bottomOffset={100} onEnter={() => setState({ isOpen: true })}>
        <ElItems
          pose={state.isOpen ? 'visible' : 'hidden'}
          style={{
            height: '100%',
          }}
          {...props}
        />
      </Waypoint>
    )}
  </Component>
);

export default Sect;
